@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.headerStyle{
    margin: 0;
    background-color: white;
    height: 90px;
    width: 100%;
    color: black;
}
.logo{
    float: left;
    text-decoration: none;
    font-size: 35px;
    height: 75px;
    width: 75px;
    margin-top: 11px;
}
.logo:hover {
    background-color: lightgrey;
}
.logo p{
    margin-top: 15px;
    margin-left: 10px;
}
.logoLetters{
    color: black;
    text-decoration: none;
}
.text-box{
    margin-top: 30px;
    margin-left: 120px;
}
.logoLetters:hover{
    color: #FF6961;
}
.groupOfLinks{
    float: right;
    background-color: white;
    margin-top: 35px;
    margin-right: 100px;
    
}
.navStyle{
    list-style-type: none;
    text-decoration: none;
    margin: 0;
    background-color: white;
    display: inline;
}

.eachLinkHeader{
    display: inline;
    padding: 30px;
    color: black;
    text-decoration:none;
    font-size: 20px;
    font-weight: 475;
}
@media only screen and (max-width: 1000px) {
    .headerStyle{
        background-color: white;
    }
    .text-box{
        margin-left: 17px;
    }
    .groupOfLinks{
      margin-right: 10px;
    }
    .navStyle{
        width: 10%;
    }
    .eachLinkHeader{
        padding: 10px;
    }
  }
@media only screen and (max-width: 850px) {
    .text-box{
        margin-left: 17px;
    }
    .groupOfLinks{
      margin-right: 10px;
    }
    .navStyle{
        width: 10%;
    }
    .eachLinkHeader{
        padding: 10px;
    }
  }
  
  @media only screen and (max-width: 600px) {
    .headerStyle{
        background-color: white;
    }
    .text-box{
        margin-left: 2px;
    }
    .groupOfLinks{
      margin-right: 2px;
    }
    .navStyle{
        width: 4%;
    }
    .eachLinkHeader{
        padding: 4px;
    }
  }

body {
  background-color: white;
  margin: 0;
  padding: 0;
  font-family: 'Mulish', sans-serif;
}

.App {
  margin: 0;
  padding: 0;
}

.basic-info{
  width: 85%;
  margin-left: 15%;
  margin-top: 10%;
}

.homepageLinks{
  padding-left: 0;
  margin-top: 100px;
  margin-left: 15%;
}

.navStyle{
  margin: 0;
  padding: 0;
}

.introName{
  display: inline;
}

.introUni{
  display: inline;
}

.eachLink{
  display: inline;
  padding-left: 0px;
  padding-right: 35px;
  color: black;
  text-decoration:none;
  font-size: 21px;
  font-weight: 500;
}

.eachLink:hover{
  color: #FF6961;
}

.link{
  margin-left: 0px;
  text-decoration: none;
  color: black;
  display: inline;
  padding-right: 5px;
}

.link:hover{
  color: #FF6961;
}

.sjsuLink{
  text-decoration: none;
}

.iconStyle{
  padding-right: 5px;
}
.projectWrapper{
  width: 100%;
  padding: 0;
  margin: 0;
}

.projectPreview{
  margin: 0;
  padding: 0;
  width: 100%;
  height: 950px;
  text-decoration: none;
  margin-top: 60px;
  background-color: white;
}

.projectLinks{
  width: 50%;
  text-decoration: none;
  color: #808080;
  font-size: 20px;
  line-height: 35px;
}

.projectLinks:hover{
  color: black;
}

.imagePreview{
  width: 70%;
  margin: auto;
  max-height: 1000px;
  max-width: 1000px;
  min-height: 200px;
  min-width: 200px;
  border-style: none;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 6px 8px 9px 2px rgba(0,0,0,0.28);
}

.imagePreview:hover{
  opacity: 0.7;
  transition: ease;
}

.textContainer{
  width: 70%;
  margin: auto;
  margin-top: 25px;
  max-height: 1000px;
  max-width: 1000px;
  min-width: 300px;
}

.projectDescription{
  font-family: 'Mulish', sans-serif;
  font-size: 23px;
  line-height: 40px;
  color: #808080;
}

.image-style{
  width: 100%;
  background-size: auto;
  background-color: black;
}

@media only screen and (max-width:1250px) {
  body {
    background-color: white;
  }
  .projectPreview{
    height: 800px;
    margin-bottom: 30px;
  }
}

@media only screen and (max-width:1150px) {
  body {
    background-color: white;
  }
  .eachLink{
    display: block;
    margin-bottom: 30px;
  }
  .projectPreview{
    height: 700px;
    margin-bottom: 30px;
  }
}

@media only screen and (max-width:950px) {
  body {
    background-color: white;
  }
  .eachLink{
    display: block;
    margin-bottom: 30px;
  }
  .projectPreview{
    height: 600px;
    margin-bottom: 40px;
  }
}

@media only screen and (max-width:850px) {
  body {
    background-color: white;
  }
  .projectPreview{
    height: 600px;
    margin-bottom: 40px;
  }
}

@media only screen and (max-width:650px) {

  .projectPreview{
    height: 500px;
    margin-bottom: 40px;
  }
}
