
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@500&display=swap');

body {
  background-color: white;
  margin: 0;
  padding: 0;
  font-family: 'Mulish', sans-serif;
}

.App {
  margin: 0;
  padding: 0;
}

.basic-info{
  width: 85%;
  margin-left: 15%;
  margin-top: 10%;
}

.homepageLinks{
  padding-left: 0;
  margin-top: 100px;
  margin-left: 15%;
}

.navStyle{
  margin: 0;
  padding: 0;
}

.introName{
  display: inline;
}

.introUni{
  display: inline;
}

.eachLink{
  display: inline;
  padding-left: 0px;
  padding-right: 35px;
  color: black;
  text-decoration:none;
  font-size: 21px;
  font-weight: 500;
}

.eachLink:hover{
  color: #FF6961;
}

.link{
  margin-left: 0px;
  text-decoration: none;
  color: black;
  display: inline;
  padding-right: 5px;
}

.link:hover{
  color: #FF6961;
}

.sjsuLink{
  text-decoration: none;
}

.iconStyle{
  padding-right: 5px;
}
.projectWrapper{
  width: 100%;
  padding: 0;
  margin: 0;
}

.projectPreview{
  margin: 0;
  padding: 0;
  width: 100%;
  height: 950px;
  text-decoration: none;
  margin-top: 60px;
  background-color: white;
}

.projectLinks{
  width: 50%;
  text-decoration: none;
  color: #808080;
  font-size: 20px;
  line-height: 35px;
}

.projectLinks:hover{
  color: black;
}

.imagePreview{
  width: 70%;
  margin: auto;
  max-height: 1000px;
  max-width: 1000px;
  min-height: 200px;
  min-width: 200px;
  border-style: none;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 6px 8px 9px 2px rgba(0,0,0,0.28);
}

.imagePreview:hover{
  opacity: 0.7;
  transition: ease;
}

.textContainer{
  width: 70%;
  margin: auto;
  margin-top: 25px;
  max-height: 1000px;
  max-width: 1000px;
  min-width: 300px;
}

.projectDescription{
  font-family: 'Mulish', sans-serif;
  font-size: 23px;
  line-height: 40px;
  color: #808080;
}

.image-style{
  width: 100%;
  background-size: auto;
  background-color: black;
}

@media only screen and (max-width:1250px) {
  body {
    background-color: white;
  }
  .projectPreview{
    height: 800px;
    margin-bottom: 30px;
  }
}

@media only screen and (max-width:1150px) {
  body {
    background-color: white;
  }
  .eachLink{
    display: block;
    margin-bottom: 30px;
  }
  .projectPreview{
    height: 700px;
    margin-bottom: 30px;
  }
}

@media only screen and (max-width:950px) {
  body {
    background-color: white;
  }
  .eachLink{
    display: block;
    margin-bottom: 30px;
  }
  .projectPreview{
    height: 600px;
    margin-bottom: 40px;
  }
}

@media only screen and (max-width:850px) {
  body {
    background-color: white;
  }
  .projectPreview{
    height: 600px;
    margin-bottom: 40px;
  }
}

@media only screen and (max-width:650px) {

  .projectPreview{
    height: 500px;
    margin-bottom: 40px;
  }
}